<template>
  <div>
      <h1>FX Rates</h1>

      <nav>
        <div class="nav nav-tabs" id="nav-tab">
          <a class="nav-item nav-link active" id="missing-fx-tab" data-toggle="tab" href="#missing-fx-panel">Missing FX Rates</a>
          <a class="nav-item nav-link" id="all-fx-tab" data-toggle="tab" href="#all-fx-panel">All FX Rates</a>
        </div>
      </nav>

      <div class="tab-content" id="nav-tabContent">
        <div class="tab-pane fade show active" id="missing-fx-panel">
          <fx-table :is-missing="isMissing"></fx-table>
        </div>
        <div class="tab-pane fade" id="all-fx-panel">
          <p>
            <br>
            <button type="button" class="btn btn-success btn-sm">Add FX Rate</button>
          </p>
          <fx-table></fx-table>
        </div>
      </div>


  </div>
</template>


<script>
import FxTable from '@/components/tables/FxTable'

export default {
  name: 'FX-Rates',
  components: {
    FxTable,
  },
  data() {
    return {
      isMissing: true,
    }
  },
}
</script>



