<template>
  <div :class="['company-card', 'company-card-' + size]">
    <logo-flag :logo="company.logo"
               :size="size"
               :countryCode="company.country.code">
    </logo-flag>
    <div class="company-card-name">{{company.name}}</div>
  </div>
</template>


<script>
import LogoFlag from './LogoFlag'

export default {
  components: {
    LogoFlag,
  },
  props: ['company', 'size'],
}
</script>



