import axios from 'axios'
import {apiToCompany} from './converters'

const API_V1_URL = process.env.API_V1_URL

export default {
  // LIST
  getList() { // ?? simple list
    return axios.get(API_V1_URL + '/Companies')
      .then(data => data.map(apiToCompany))
  },
  getTree() {
    return axios.get(API_V1_URL + '/CompaniesTree')
      .then(data => {
        console.log(data)
        return data
      })
  },
  // CRUD
  create(companyData) {
    return axios.post(API_V1_URL + '/Companies', companyData)
      .then(data => apiToCompany(data))
  },
  getCompany(id) {
    return axios.get(API_V1_URL + '/Companies/' + id)
      .then(data => apiToCompany(data))
  },
  update(company) {
    const data = company // TODO: convert back
    return axios.patch(API_V1_URL + '/Companies/' + company.id, data)
  },
  delete(id) {
    return axios.delete(API_V1_URL + '/Companies/' + id)
  },
  // Misc
  uploadLogo(id, imgFile) {
    let fd = new FormData()
    fd.append('file', imgFile)
    return axios.post(API_V1_URL + '/Companies/' + id + '/Logo', fd)
  },
}



