import axios from 'axios'

const API_V1_URL = process.env.API_V1_URL
const ALIASES_URL = API_V1_URL + '/Aliases'

export default {
  list(companyId, refCompanyID) {
    return axios.get(ALIASES_URL + '/Companies/' + companyId + 'RefCompanies/' + refCompanyID + '/Aliases')
  },
  create(companyId, refCompanyID, aliasText) {
    return axios.post(ALIASES_URL, {companyId, refCompanyID, aliasText})
  },
  update(id, aliasText) {
    return axios.patch(ALIASES_URL + id, {id, aliasText})
  },
  delete(id) {
    return axios.delete(ALIASES_URL + id)
  },
}



