<template>
  <div class="table-responsive">
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">Name</th>
          <th scope="col">Aliases</th>
          <th scope="col">Reg Number</th>
          <th scope="col">Reporting Currency</th>
          <th scope="col">Direct Parent</th>
          <th scope="col">Top Parent</th>
          <!-- <th scope="col">Notifications</th> -->
          <th scope="col">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="company in companies">
          <th>
            <router-link :to="'/company/' + company.id">
              <company-card :company="company" size="m"></company-card>
            </router-link>
          </th>
          <td>{{ company.aliases.map(i => i.name).join(', ') }}</td>
          <td>{{ company.regNum }}</td>
          <td>{{ company.currency.code }}</td>
          <td>{{ company.parent.name }}</td>
          <td>{{ company.topParent }}</td>
          <!-- <td>{{ company.notifications }}</td> -->
          <td>
            <div class="dropdown">
              <button class="btn btn-secondary btn-sm" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">...</button>
              <div class="dropdown-menu dropdown-menu-right">
                <a class="dropdown-item" href="#">Upload</a>
                <a class="dropdown-item" href="#">Start Reconciliation</a>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
import CompanyCard from '../CompanyCard'

export default {
  components: {
    CompanyCard,
  },
  props: ['companies'],
}
</script>



