<template>
  <div class="container-fluid">
      <h1>Nomenclatures</h1>

      <h3>Countries</h3>
      <table>
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Code</th>
            <th>Order</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="country in countries">
            <td>{{ country.id }}</td>
            <td>{{ country.name }}</td>
            <td>{{ country.code }}</td>
            <td>{{ country.order }}</td>
          </tr>
        </tbody>
      </table>

      <h3>Currencies</h3>
      <table>
        <thead>
          <tr>
            <th>Id</th>
            <th>Name</th>
            <th>Code</th>
            <th>Order</th>
          </tr>
        </thead>

        <tbody>
          <tr v-for="currency in currencies">
            <td>{{ currency.id }}</td>
            <td>{{ currency.name }}</td>
            <td>{{ currency.code }}</td>
            <td>{{ currency.order }}</td>
          </tr>
        </tbody>
      </table>

      <loading :loading="loading"></loading>

  </div>
</template>


<script>
export default {
  created() {
    this.loading = true

    const loadCountriesPromise = this.$store.dispatch('loadCountries')
    const loadCurrenciesPromise = this.$store.dispatch('loadCurrencies')

    Promise.all([loadCountriesPromise, loadCurrenciesPromise])
      .then(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    countries() {
      return this.$store.getters.getCountries
    },
    currencies() {
      return this.$store.getters.getCurrencies
    },
  },
}
</script>



