import companyService from '@/services/companyService'
import aliasService from '@/services/aliasService'
import personnelService from '@/services/personnelService'

const state = {
  selectedCompany: null,
  list: null,
}

const getters = {
  selectedCompany(state) {
    return state.selectedCompany
  },
  companiesList(state) {
    return state.list
  },
}

const mutations = {
  setList(state, payload) {
    state.list = payload
  },
  // CRUD
  addCompany(state, payload) {
    // TODO: add company to list and tree
  },
  selectCompany(state, payload) {
    state.selectedCompany = state.list[payload]
  },
  updateSelCompany(state, payload) {
    state.selectedCompany = payload
  },
  deleteSelCompany(state) {
    const index = state.list.indexOf(state.selectedCompany)
    state.list.splice(index, 1)
    state.selectedCompany = null
  },
  // SELECTED COMPANY PROPERTIES
  setSCLogo(state, payload) {
    // TODO: later
    state.selectedCompany.logo.src = payload
  },
  addSCAlias(state, payload) {
    state.selectedCompany.aliases.push(payload)
  },
  updateSCAlias(state, {alias, text}) {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(alias)
    aliases[index].alias = text
  },
  deleteSCAlias(state, payload) {
    var aliases = state.selectedCompany.aliases
    const index = aliases.indexOf(payload)
    aliases.splice(index, 1)
  },
}

const actions = {
  loadCompanies({ commit, state }) {
    if (state.list) {
      return Promise.resolve(state.list)
    }

    return companyService.getList()
      .then(list => {
        commit('setList', list)
        return list
      })
  },
  // CRUD
  createCompany({ commit }, companyData) {
    return companyService.create(companyData)
      .then(company => {
        commit('addCompany', company)
        return company
      })
  },
  loadSelCompany({ commit, state }, {id, index}) {
    if (state.selectedCompany && state.selectedCompany.id === id) {
      return Promise.resolve(state.selectedCompany)
    }

    // TODO: check if a company from the list was already a selected company

    return companyService.getCompany(id)
      .then(company => {
        // TODO: add the company to the list
        // and get the index
        commit('selectCompany', index)
        return company
      })
  },
  updateSelCompany({ commit }, companyData) {
    return companyService.update(companyData)
      .then(company => {
        commit('updateSelCompany', company)
        return company
      })
  },
  deleteSelCompany({ commit, state }) {
    return companyService.delete(state.selectedCompany.id)
      .then(() => {
        commit('deleteCompany')
      })
  },
  // COMPANY PROPERTIES
  uploadSCLogo({ commit, state }, imgFile) {
    return companyService.uploadLogo(state.selectedCompany.id, imgFile)
      .then(url => {
        commit('setSCLogo', url)
      })
  },
  // ALIASES
  loadSCRefCompAliases({ commit, state }, refCompanyID) {
    // This will be needed when we develop the Address Book
    return aliasService.list(state.selectedCompany.id, refCompanyID)
      .then(aliases => {
        commit('setSCRefCompAliases', aliases)
      })
  },
  createSCAlias({ commit, state }, alias) {
    const id = state.selectedCompany.id
    return aliasService.create(id, id, alias)
      .then(alias => {
        commit('addSCAlias', alias)
      })
  },
  updateSCAlias({ commit }, {alias, text}) {
    return aliasService.update(alias.id, text)
      .then(() => {
        commit('updateSCAlias', {alias, text})
      })
  },
  deleteSCAlias({ commit }, alias) {
    return aliasService.delete(alias.id)
      .then(() => {
        commit('removeSCAlias', alias)
      })
  },
  // PERSONNEL
  createSCPersonnel({ commit, state }, personnel) {
    // TODO
    return personnelService.create()
  },
  updateSCPersonnel({ commit }, alias) {
    // TODO
    return personnelService.update()
  },
  deleteSCPersonnel({ commit }, alias) {
    // TODO
    return personnelService.delete()
  },
}

export default {
  state,
  getters,
  mutations,
  actions,
}



