<template>
  <div class="table-responsive">
    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">Date</th>
          <th scope="col">Type</th>
          <th colspan="3">Currency</th>
          <th scope="col">Rate</th>

          <template v-if="!isMissing">
            <th scope="col">Input Method</th>
            <th scope="col">Source</th>
            <th scope="col"></th>
          </template>
        </tr>
      </thead>
      <tbody>
        <tr>
          <th>31 Jan `15</th>
          <td>Daily</td>
          <td>
            <img class="flag" :src="'/static/flags/' + mockFx.currency.country + '.svg'" target="_blank">
          </td>
          <td>{{ mockFx.currency.code }}</td>
          <td>US dollar</td>

          <template v-if="isMissing">
            <td>
              <input type="text" placeholder="1.2345">
              <button type="button" class="btn btn-success btn-sm">Save</button>
            </td>
          </template>

          <template v-if="!isMissing">
            <td>{{ mockFx.rate }}</td>
            <td>Auto/Manual</td>
            <td>
              <a href="http://www.ecb.europa.eu/stats/policy_and_exchange_rates/euro_reference_exchange_rates/html/index.en.html">ECB</a>
            </td>
            <td>
              <button type="button" class="btn btn-info btn-sm" @click="edit(mockFx)">Edit</button>
              <button type="button" class="btn btn-danger btn-sm" @click="remove(mockFx)">Delete</button>
            </td>
          </template>

        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
export default {
  props: ['isMissing'],
  data() {
    return {
      loading: false,
      countryCode: 'us',
      mockFx: {
        id: 1,
        currency: {
          id: 1,
          code: 'USD',
          country: 'us',
        },
        rate: 1.2345,
      },
    }
  },
  methods: {
    edit(fx) {
      let name = window.prompt('FX Rate:', fx.rate)
      if (name) {
        this.loading = true
        // this.$store.dispatch('updateGroup', {id: group.id, name})
        //   .finally(() => {
        //     this.loading = false
        //   })
      } else if (name === '') {
        window.alert('The name can not be empty!')
      }
    },
    remove(fx) {
      if (window.confirm(`Are you sure you want to delete ...<TODO>...`)) {
        this.loading = true
        // this.$store.dispatch('deleteGroup', group.id)
        //   .finally(() => {
        //     this.loading = false
        //   })
      }
    },
  },
}
</script>



