<template>
  <div :class="['logo', 'logo-' + size]">
    <img v-if="logo.src" :src="logo.src">
    <div v-else>{{ logo.initials }}</div>
    <img class="flag" :src="'/static/flags/' + countryCode + '.svg'" target="_blank">
  </div>
</template>


<script>
  export default {
    props: {
      logo: {},
      size: {},
      countryCode: {
        default: 'us',
      },
    },
    methods: {
      style(code) {
        return {
          background: 'url(/static/flags/' + code + '.svg)',
        }
      },
    },
  }
</script>



