import axios from 'axios'

const API_V1_URL = process.env.API_V1_URL
const PERSONNEL_URL = API_V1_URL + '/Personnel'

export default {
  create(companyId, responsibility, name, email, phone) {
    return axios.post(PERSONNEL_URL, {companyId, responsibility, name, email, phone})
  },
  update(id, personnelData) {
    return axios.patch(PERSONNEL_URL + id, personnelData)
  },
  delete(id) {
    return axios.delete(PERSONNEL_URL + id)
  },
}



