<template>
  <div>
      <h1>Companies</h1>
      <companies-table :companies="companiesList" v-if="companiesList"></companies-table>
  </div>
</template>


<script>
import CompaniesTable from '@/components/tables/CompaniesTable'

export default {
  components: {
    CompaniesTable,
  },
  created() {
    this.loading = true
    this.$store.dispatch('loadCompanies')
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    companiesList() {
      return this.$store.getters.companiesList
    },
  },
}
</script>



